import emailjs from '@emailjs/browser';
import { SectionWrapper } from "../hoc";


import React, { useRef } from "react";
import { motion } from "framer-motion";
import { useState } from 'react';
import { styles } from "../styles";
import { EarthCanvas } from "./canvas";
import { slideIn } from "../utils/motion";
import { construction } from "../assets";

export const ContactUs = () => {
  const form = useRef();
  

  const sendEmail = (e) => {
    e.preventDefault();
    setLoading(true);


    emailjs.sendForm('service_1len03q', 'template_wnbc1ul', form.current, '3ppC-9HBke7DKDOjB')
      .then((result) => {
        setLoading(false);
        alert("Thank you. I will get back to you as soon as possible.");

        setForm({
          name: "",
          email: "",
          message: "",
        });
      }, (error) => {
        setLoading(false);
        console.error(error);

        alert("Ahh, something went wrong. Please try again.");
      });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true);

    emailjs
      .then(
        () => {
          setLoading(false);
          alert("Thank you. I will get back to you as soon as possible.");

          setForm({
            name: "",
            email: "",
            message: "",
          });
        },
        (error) => {
          setLoading(false);
          console.error(error);

          alert("Ahh, something went wrong. Please try again.");
        }
      );
  };


  const formRef = useRef();
  const [formm, setForm] = useState({
    name: "",
    email: "",
    message: "",
  });

  const [loading, setLoading] = useState(false);

  const handleChange = (e) => {
    const { target } = e;
    const { name, value } = target;

    setForm({
      ...form,
      [name]: value,
    });
  };

  return (
    <div className='violett-gradient'>
  

    <form
    ref={formRef}
    onSubmit={sendEmail}
    className='mt-12 flex flex-col gap-8'
  >
    <label className='flex flex-col'>
      <span className='text-white font-medium mb-4'>Your Name</span>
      <input
        type='text'
        name='name'
        value={formm.name}
        onChange={handleChange}
        placeholder="What's your good name?"
        className='bg-tertiary py-4 px-6 placeholder:text-secondary text-white rounded-lg outline-none border-none font-medium'
      />
    </label>
    <label className='flex flex-col'>
      <span className='text-white font-medium mb-4'>Your email</span>
      <input
        type='email'
        name='email'
        value={formm.email}
        onChange={handleChange}
        placeholder="What's your web address?"
        className='bg-tertiary py-4 px-6 placeholder:text-secondary text-white rounded-lg outline-none border-none font-medium'
      />
    </label>
    <label className='flex flex-col'>
      <span className='text-white font-medium mb-4'>Your Message</span>
      <textarea
        rows={7}
        name='message'
        value={formm.message}
        onChange={handleChange}
        placeholder='What you want to say?'
        className='bg-tertiary py-4 px-6 placeholder:text-secondary text-white rounded-lg outline-none border-none font-medium'
      />
    </label>

    <button
      type='submit'
      className='bg-tertiary py-3 px-8 rounded-xl outline-none w-fit text-white font-bold shadow-md shadow-primary'
    >
      {loading ? "Sending..." : "Send"}
    </button>
  </form>
  </div>
  );
};
export default SectionWrapper(ContactUs, "ContactUs");
